import Vue from 'vue'
// import Login from './components/Login.vue'
import {createRouter,createWebHashHistory} from "vue-router";
const Login = () => import(/* webpackChunkName: "login_home_welome" */ 'components/Login')
const Home = () => import(/* webpackChunkName: "login_home_welome" */ 'components/Home')
const Welcome = () => import(/* webpackChunkName: "login_home_welome" */ './components/Welcome.vue')

const Users = () => import(/* webpackChunkName: "Users_Rights_Roles" */ './components/user/Users.vue')
const Rights = () => import(/* webpackChunkName: "Users_Rights_Roles" */ './components/power/Rights.vue')
const Roles = () => import(/* webpackChunkName: "Users_Rights_Roles" */ './components/power/Roles.vue')

const Cate = () => import(/* webpackChunkName: "Cate_Params" */ './components/goods/Cate.vue')
const Params = () => import(/* webpackChunkName: "Cate_Params" */ './components/goods/Params.vue')

const List = () => import(/* webpackChunkName: "GoodsList_Add" */ './components/goods/List.vue')
const Add = () => import(/* webpackChunkName: "GoodsList_Add" */ './components/goods/Add.vue')

const Order = () => import(/* webpackChunkName: "Order_Report" */ './components/order/Order.vue')
const Report = () => import(/* webpackChunkName: "Order_Report" */ './components/report/Report.vue')
// import Home from './components/Home.vue'
// import Welcome from './components/Welcome.vue'
// import Users from './components/user/Users.vue'
// import Rights from './components/power/Rights.vue'
// import Roles from './components/power/Roles.vue'
// import Cate from './components/goods/Cate.vue'
// import Params from './components/goods/Params.vue'
// import List from './components/goods/List.vue'
// import Add from './components/goods/Add.vue'
// import Order from './components/order/Order.vue'
// import Report from './components/report/Report.vue'

const router = createRouter({
    history:createWebHashHistory(),
    routes:[
        {
            path:"/",
            redirect:'/Login'
        },
        {
            path:"/login",
            component:Login
        },{
            path:"/home",
            component:Home,
            redirect:'/welcome',
            children:[
                {
                    path:'/welcome',
                    component:Welcome
                },
                {
                    path: '/users',
                    component: Users
                },
                {
                    path: '/rights',
                    component: Rights
                },
                {
                    path: '/roles',
                    component: Roles
                },
                {
                    path: '/categories',
                    component: Cate
                },
                {
                    path: '/params',
                    component: Params
                },
                {
                    path: '/goods',
                    component: List
                },
                {
                    path: '/goods/add',
                    component: Add
                },
                {
                    path: '/orders',
                    component: Order
                },
                {
                    path: '/reports',
                    component: Report
                }
            ]
        }
    ]
});

//导航守卫、路由守卫、路由拦截
router.beforeEach((to,from,next)=>{
    //console.log(to);//要去的路径
    //console.log(from);//从哪来的路径
    // next();是一个函数,表示放行
    // next('/login') 强制跳转
    //验证token,只有存在token的时候，才能跳转到内容页。
    let tokenStr = window.sessionStorage.getItem("token");
    if(tokenStr||to.path==="/login"){
        next();
    }else{
        next("/login")
    }
})

export default router;

